.uploader {
  text-align: center;
}
.uploader label {
  display: block;
  padding: 10px;
}

:root {
  --background-color: #9fedd7;
  --tweets: #edeae5;
  --tweetBox: #fef9c7;
}
.list-item {
  list-style-type: none;
  padding: 25px 0 25px 0;
  margin-top: 5px;
  display: -webkit-flex;
  display: flex;
  height: 50px;
  background-color: #edeae5;
  background-color: var(--tweets);
  border-radius: 10px 0 10px 0;
}
.tweet-picture-container {
  height: 50px;
  margin-left: 5px;
}
.tweet-picture-container img {
  height: 50px;
  border-radius: 10px;
}
.tweet-content p {
  margin: 0;
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
}
.tweet-username {
  padding-right: 20px;
}
.tweet-time::before {
  content: " ~";
}
.tweet-content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding-left: 20px;
  width: 100%;
